import React from "react";

const CategoryItem = ({ data }) => {
  return (
    <div className="flex flex-col items-center justify-center p-2 text-center transition-all border border-gray-100 cursor-pointer hover:border-gray-300">
      <img src={data.img} alt={data.title} className="w-[85px] h-[85px]" />
      <p className="text-sm text-[rgba(0,_0,_0,_0.8)] capitalize">
        {data.title}
      </p>
    </div>
  );
};

export default CategoryItem;
