import Banner from "modules/shopeeContainer/banner/Banner";
import Category from "modules/shopeeContainer/category/Category";
import ListItem from "modules/shopeeContainer/ShopeeItem/ListItem";
import React from "react";
import { useEffect } from "react";
import { Fragment } from "react";

const ShopeePage = () => {
  useEffect(() => {
    document.title =  "Shopee Việt Nam";
  }, []);
  return (
    <Fragment>
      <div className="max-w-[1280px] mx-auto my-7 z-0 flex flex-col gap-y-8 ">
        <Banner></Banner>
        <Category></Category>
        <ListItem></ListItem>
      </div>
    </Fragment>
  );
};

export default ShopeePage;
