import React from "react";
// Import Swiper styles
import "swiper/css";
import "swiper/css/pagination";
// import required modules
import { Navigation, Pagination } from "swiper";
import { SwiperSlide, Swiper } from "swiper/react";

const bannerContainer = [
  {
    id: 1,
    src: "https://cf.shopee.vn/file/6d0973d13c0d8c293f7fdf3f0121dab2_xxhdpi",
  },
  {
    id: 2,
    src: "https://cf.shopee.vn/file/371dfc092c125d0e51d06e8b7e4343ba_xxhdpi",
  },
  {
    id: 3,
    src: "https://cf.shopee.vn/file/9f50fcafe6db9cbacb0ce8a0599f8cce_xxhdpi",
  },
  {
    id: 4,
    src: "https://cf.shopee.vn/file/8e924cf3367704a0bac972df166e17fa_xxhdpi",
  },
];

const SlideBanner = () => {
  return (
    <div className="slide-banner lg:w-[800px] md:w-[820px] w-screen  sm:h-[245px] h-[118px]">
      <div className="z-10 flex items-center justify-center w-full h-full overflow-hidden rounded">
        <Swiper
          loop={true}
          navigation={true}
          pagination={true}
          modules={[Navigation, Pagination]}
        >
          {bannerContainer.length > 0 &&
            bannerContainer.map((item, index) => (
              <SwiperSlide key={item.id}>
                <img
                  src={item.src}
                  alt={item.id}
                  className="z-0 object-cover w-full h-full rounded select-none"
                />
              </SwiperSlide>
            ))}
        </Swiper>
      </div>
    </div>
  );
};

export default SlideBanner;
