import { FakeDataCategory } from "data/FakeDataCategory";
import React from "react";
import { v4 } from "uuid";
import CategoryItem from "./CategoryItem";

const Category = () => {
  const data = FakeDataCategory;
  return (
    <div className="w-full pt-5 bg-white">
      <h3 className="text-[rgba(0,_0,_0,_0.54)] text-[16px] leading-5 px-5 uppercase mb-5">
        Danh Mục
      </h3>
      <div className="grid grid-cols-4 overflow-hidden lg:grid-cols-10">
        {data.length > 0 &&
          data.map((data) => (
            <CategoryItem data={data} key={v4()}></CategoryItem>
          ))}
      </div>
    </div>
  );
};

export default Category;
